<template>
  <div class="row mt-3">
    <div class="col-md-6 offset-md-3">
      <div class="card mb-3">
        <div class="card-header bg-dark text-white text-center">Registrar Estudiante</div>
        <div class="card-body text-center">
          <form v-on:submit="addEstudiante">
            <div class="d-grid col-6 mx-auto mb-3">
              <img v-if="this.foto" height="100" :src="this.foto" id="fotoimg" class="img-thumbnail" />
              <img v-else height="100" id="fotoimg" src="https://cdn1.iconfinder.com/data/icons/user-pictures/101/malecostume-256.png" class="img-thumbnail" alt="" /> 
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-user"></i></span>
              <input type="text" v-model="nombre" id="nombre" class="form-control" placeholder="Nombre" maxlength="50" required />
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-user"></i></span>
              <input type="text" v-model="apellidos" id="apellidos" class="form-control" placeholder="Apellidos" maxlength="50" required />
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-camera-retro"></i></span>
              <input type="file" v-on:change="previsualizarFoto" class="form-control" accept="image/png, image/jpeg, image/jpg, image/gif" />
            </div>
            <div class="d-grid col-4 mx-auto mb-1">
              <button class="btn btn-success"><i class="fa-solid fa-floppy-disk"></i> REGISTRAR</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  import { mostrarAlerta, enviarSolicitud } from '../funciones.js';

  export default {
    data(){
      return {
        nombre: '',
        apellidos: '',
        foto: '',
        url: `${process.env.VUE_APP_URL_BACKEND}/api/v1/estudiantes`,
        cargando: false
      }
    },
    methods:{
      addEstudiante(){
        event.preventDefault();
        var miFoto = document.getElementById('fotoimg');
        this.foto = miFoto.src;

        if(this.nombre.trim()===''){
          mostrarAlerta('Debe ingresar un nombre', 'warning', 'nombre');
        }else if(this.apellidos.trim()===''){
          mostrarAlerta('Debe ingresar un apellido', 'warning', 'apellidos');
        }else{
          var parametros = {nombre: this.nombre.trim(), apellidos: this.apellidos.trim(), foto: this.foto.trim()}
          enviarSolicitud('POST', parametros, this.url, 'Estudiante registrado exitosamente!');
        }
        
      },
      previsualizarFoto(e){
        var reader = new FileReader();
        reader.readAsDataURL(e.target.files[0]);
        reader.onload = function(){
          var miFoto = document.getElementById('fotoimg');
          miFoto.src = reader.result;
          this.foto = miFoto.src;
        }
      }
    }
  }
</script>